
@font-face {
  font-family: 'Lato';
  font-weight: 400;
  font-style: normal;
  src: url('assets/fonts/Lato-regular/Lato-regular.eot');
  src: url('assets/fonts/Lato-regular/Lato-regular.eot?#iefix') format('embedded-opentype'),
       local('Lato Regular'),
       local('Lato-regular'),
       url('assets/fonts/Lato-regular/Lato-regular.woff2') format('woff2'),
       url('assets/fonts/Lato-regular/Lato-regular.woff') format('woff'),
       url('assets/fonts/Lato-regular/Lato-regular.ttf') format('truetype'),
       url('assets/fonts/Lato-regular/Lato-regular.svg#Lato') format('svg');
}
@font-face {
  font-family: 'Raleway';
  font-weight: 400;
  font-style: normal;
  src: url('assets/fonts/Raleway-regular/Raleway-regular.eot');
  src: url('assets/fonts/Raleway-regular/Raleway-regular.eot?#iefix') format('embedded-opentype'),
       local('Raleway'),
       local('Raleway-regular'),
       url('assets/fonts/Raleway-regular/Raleway-regular.woff2') format('woff2'),
       url('assets/fonts/Raleway-regular/Raleway-regular.woff') format('woff'),
       url('assets/fonts/Raleway-regular/Raleway-regular.ttf') format('truetype'),
       url('assets/fonts/Raleway-regular/Raleway-regular.svg#Raleway') format('svg');
}
body {
  background-image: url("./assets/background.jpg");
  background-attachment: fixed;
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow: hidden;
  width: 100%;
}
.flexbox {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  overflow: hidden;
  min-height: 100vh;
}
#sidebar {
    background-color: #fff;
    box-shadow: 4px 0px 2px #fff;
    float: left;
    width: 20vmax;
    min-height: 100vh;
    text-align: center;
}
.menu-img {
  height: 9vmin;
}
#links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.link{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-item-align: center;
      align-self: center;
}
#links a {
  color: #000;
  text-decoration: none;
}
.router-link-active {
  color: #0D4A5C !important;
  border-bottom: 2px solid #0D4A5C;
}
#content {
  margin: 0 0 0 4px;
  min-height: 100vh;
  width: 80vmax;
}
#main-logo {
  height: 13vmax;
  width:  20vmax;
}
footer {
  background-color: #fff;
  bottom: 0;
  color: #000;
  position: relative;
}
.or-spacer {
  width: 20vmax;
  position: relative;
}
.or-spacer .mask {
  overflow: hidden;
  height: 20px;
}
.or-spacer .mask:after {
  content: '';
  display: block;
  margin: -25px auto 0;
  width: 100%;
  height: 25px;
  border-radius: 125px / 12px;
  box-shadow: 0 0 8px black;
}
.fa {
  color:  #000;
  padding: 4px;
}




